import * as types from '../mutation-types'
import profApi from '../../api/profApi'
import router from '../../router'
import Axios from 'axios'

const state = {
	
}

const getters = {
}

const actions = {
	initProfile({commit, dispatch}, payload) {
		profApi.getProfile(payload, data => {
			// data = {username:'test', email:'test'}
			commit(types.INIT_PROFILE, data);
		})
	},
	updateinfo ({commit, dispatch}, payload) {
		profApi.updateinfo(payload, data => {
			if (data == 1) {
				dispatch('initProfile')
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'updateinfo'})
			} else if(data == 0){
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':'', 'msgcontenttype': 'updateinfo'})
			}
			else {
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'warning', 'content':'', 'msgcontenttype': 'updateinfo'})
			}
		})
	},
	// updateEmail ({commit, dispatch}, payload) {
	// 	profApi.updateEmail(payload, data => {
	// 		if (data.profId > 0) {
	// 			dispatch('initProfile')
	// 			commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':''})
	// 		} else {
	// 			commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':''})
	// 		}
	// 	})
	// },
	updatepwd ({commit, dispatch}, payload) {
		profApi.updatePassword(payload, data => {
			if (data == 1) {
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'updatepwd'});
			} else if (data == 0) {
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':'', 'msgcontenttype': 'updatepwd'})
			} else {
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':'',  'msgcontenttype': 'updatepwd'})
			}
		})
	},
	// updateImage ({commit, dispatch}, payload) {
	// 	profApi.updateImage(payload, data => {
	// 		if (data.profId > 0) {
	// 			dispatch('initProfile')
	// 			commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':''})
	// 		} else {
	// 			commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':''})
	// 		}
	// 	})
	// },
}

const mutations = {
	
}

export default {
	state,
	getters,
	actions,
	mutations
}



