import * as types from '../mutation-types'
import userApi from '../../api/userApi'
import router from '../../router'
import Axios from 'axios';

const state = {
	user_id: 0,
}

const getters = {
	user_id: state => state.user_id,
}

const actions = {
	setLoggedStatuswithCookie(){
		var d = new Date();
		d.setTime(d.getTime() + 60 * 60 * 1000);
		const expires = `expires=${d.toUTCString()}`;
		document.cookie = 'login=yes;' + expires + ';path=/'
	},
	removeLoggedStatusWithCookie(){
		var d = new Date();
		d.setTime(d.getTime() - 60 * 60 * 1000);
		const expires = `expires=${d.toUTCString()}`;
		document.cookie = 'login=no;' + expires + ';path=/'
	},
	isToLogin({dispatch}, payload){
		dispatch('setLoggedStatuswithCookie');
		localStorage.setItem('JWT', payload.token);
	},
	isToLogout({dispatch, commit}){
		dispatch('removeLoggedStatusWithCookie');
		localStorage.removeItem('JWT');
		commit(types.LOG_OUT);
		delete Axios.defaults.headers.common['Authorization'];
	},
	login ({commit, dispatch}, payload) {
		userApi.login(payload, data => {
			if ('token' in data) {
				//dispatch('initProfile', {user_id:data.user_id});
				dispatch('isToLogin', {token:data.token});
				Axios.defaults.headers.common['Authorization'] = data.token;
				commit(types.IS_LOGIN, data.token);
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'login'});
			} else {
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'error', 'content':'', 'msgcontenttype': 'login'});
				localStorage.removeItem('JWT')
			}
		})
	},
	logout ({commit, dispatch}) {
		console.log('logout');
		commit(types.IS_LOGOUT)
		dispatch('isToLogout');
		commit(types.RESET_PROFILE);
		commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'logout'})
	},
	register({commit, dispatch}, payload){
		userApi.register(payload, data => {
			if (data > 0){
				// dispatch('initProfile')
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'register'})
			} else{
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'warning', 'content':'', 'msgcontenttype': 'register'})
			}
		})
	},
	forget({commit, dispatch}, payload){
		userApi.forget(payload, data => {
			if (data > 0){
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'success', 'content':'', 'msgcontenttype': 'forget'})
			} else{
				commit(types.SHOW_TOP_POPUP, {'msgtype': 'warning', 'content':'', 'msgcontenttype': 'forget'})
			}
		})
	}
}

const mutations = {
	
}

export default {
	state,
	getters,
	actions,
	mutations
}



